import { Component, OnInit } from '@angular/core';
import { SendInformationService } from '../../service/SendInformation/send-information.service';
import { Observable } from 'rxjs';
import { LOADING } from '../../CONST';

@Component({
  selector: 'app-loader',
  templateUrl: './loader.component.html',
  styleUrls: ['./loader.component.scss', '../../../../../assets/cam/scss/components/common/_loader.scss']
})
export class LoaderComponent {

  data: {
    mostrar?: boolean;
    header?: string;
    footer?: string;
  };

  constructor(
    private loadingModalService: SendInformationService<{
      mostrar?: boolean;
      header?: string;
      footer?: string;
    }>,
  ) {
    this.loadingModalService.getData(LOADING).subscribe( this.response());
  }

  response(): (response: {
    mostrar?: boolean;
    header?: string;
    footer?: string;
  }) => void {
    return ( response: {
      mostrar?: boolean;
      header?: string;
      footer?: string;
    }) => {
      this.data = response;
    };
  }

}
