import { Component, OnInit } from '@angular/core';
import { Modal } from '../../modal/modal';
import { CatalogoService } from '../../../service/catalogo/catalogo.service';
import { SendInformationService } from '../../../service/SendInformation/send-information.service';
import { LENGUAJE, PAIS, MODULO } from '../../../CONST';
import { TranspilleMessagePipe } from '../../../pipe/transpilleMessage/transpille-message.pipe';

@Component({
  selector: 'app-info-modal-tma',
  templateUrl: './info-modal-tma.component.html',
  styleUrls: [
    './info-modal-tma.component.scss',
    '../../../../../../assets/cam/scss/components/modal/_infoModal.scss'
  ]
})
export class InfoModalTMAComponent implements OnInit {

  listo: boolean;
  data: Modal = {
    buttons: [
      {
        callback: null,
        buttonText: ''
      }
    ],
    title: '',
    message: '',
    display: false,
    entryComponent: null
  };
  show: boolean;

  constructor(
    private catalogo: CatalogoService,
    private obser: SendInformationService<any>
  ) {
    this.listo = false;
    setTimeout( () => this.show = true, 120);
  }

  ngOnInit(): void {
    this.catalogo.assetsTextV2(this.data.message).subscribe(
      response => {
        if ( response ) {
          this.data.message = response;
        }
      },
      err => this.listo = true,
      () => this.listo = true
    );
  }

  destroy: any = () => {};

  onClose() {
    return () => {
      this.data.display = false;
      setTimeout( () => {
        this.destroy();
      }, 250);
    };
  }

}
