import { Component } from '@angular/core';
import { fadeAnimation } from './app.outlet';
import { PostMessagesService } from './modules/common/service/postMessages/post-messages.service';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss', '../assets/cam/scss/components/_app.scss'],
  animations: [fadeAnimation]
})
export class AppComponent {

  display: any;
  previusY: number;
  currentY: number;
  go: boolean;

  constructor(
    private postMessage: PostMessagesService,
    private translate: TranslateService
  ) {
    this.translate.setDefaultLang('es-CAM');
    this.previusY = 0;
    this.currentY = 0;
  }

  scrollStart(): void {
    this.go = true;
  }

  scrollStop(): void {
    this.go = false;
  }

  moveScroll(): void {
    window.scroll(0, 0);
  }

  evento(evento: any): void {
    if ( evento.path.length >= 9 ) {
      const display = evento.path[evento.path.length - 7];
      this.display = display;
      this.previusY = this.currentY;
      this.currentY = evento.pageY;
      if ( this.go === true ) {
        this.display.scrollTo(0, display.scrollTop + parseInt(`${(this.currentY - this.previusY) * 1.25}`, 10));
      }
    }
  }

  changePage(): void {
    this.postMessage.appFinish('', 'op_activity');
  }
}
