import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent, HttpResponse, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map, concatMap, retry, skipWhile } from 'rxjs/operators';
import { IP } from '../../../CONST';
import { SendInformationService } from '../../SendInformation/send-information.service';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class IpService implements HttpInterceptor {

  mbaasPkeyUrl: string;
  ip: boolean;

  constructor(
    private obser: SendInformationService<string>
  ) {
    this.obser.sendData('127.0.0.1', IP);
    this.mbaasPkeyUrl = environment.mbaas_pkey_url;
    this.ip = environment.interceptors.ip;
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    if (
      this.ip &&
      this.obser.lastValue(IP) === '127.0.0.1' &&
      req.url === this.mbaasPkeyUrl
    ) {
      const requestClone = req.clone();
      const replaceRequest = req.clone({
        method: 'GET',
        url: environment.ip,
        headers: new HttpHeaders()
      });
      return next.handle(replaceRequest).pipe(
        retry(5),
        skipWhile( this.skipWhile() ),
        map( this.map() ),
        concatMap(this.responseConcatMap(requestClone, next))
      );
    }
    return next.handle(req);
  }

  skipWhile(): (value: HttpResponse<any>) => any {
    return (value: HttpResponse<any>) => !!!value.body;
  }

  map(): (data: HttpResponse<any>) =>  any {
    return (data: HttpResponse<any>) => data.body;
  }

  responseConcatMap(req: HttpRequest<any>, next: HttpHandler): (result: any) => Observable<HttpEvent<any>> {
    return (result: any) => {
       this.obser.sendData(result.ip, IP);
       return next.handle(req);
     };
   }
}
