import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'mbaas-logger-item',
  templateUrl: './logger-item.component.html',
  styleUrls: ['./logger-item.component.scss']
})
export class LoggerItemComponent implements OnInit {

  ocultarB: boolean;
  @Input() data: any;
  @Output() oculto = new EventEmitter();
  constructor() {
    this.ocultarB = false;
  }

  ngOnInit() {
    this.oculto.emit(true);
  }

  ocultar(): void {
    this.data.number = 5;
    this.ocultarB = true;
    setTimeout(() => this.data.number = 4, 1000);
    setTimeout(() => this.data.number = 3, 2000);
    setTimeout(() => this.data.number = 2, 3000);
    setTimeout(() => this.data.number = 1, 4000);
    setTimeout(() => this.oculto.emit(this.ocultarB ? false : true), 5000);
  }

  mostrar(): void {
    this.ocultarB = false;
  }

}
