import { Component, OnInit } from '@angular/core';
import { environment } from 'src/environments/environment';

import { Logger } from './logger';
import { SendInformationService } from 'src/app/modules/common/service/SendInformation/send-information.service';
import { LOGGER } from 'src/app/modules/common/CONST';

@Component({
  // tslint:disable-next-line: component-selector
  selector: 'mbaas-logger',
  templateUrl: './logger.component.html',
  styleUrls: ['./logger.component.scss']
})
export class LoggerComponent implements OnInit {

  log: Array<Logger>;
  isLogger: boolean;
  show: boolean;
  fade: boolean;

  constructor(
    public logger: SendInformationService<Logger>
  ) {
    this.log = [];
    this.isLogger = environment.logger;
  }

  mostrar(): void {
    this.show = true;
    setTimeout(() => this.fade = true, 50);
  }

  ocultar(): void {
    this.fade = false;
    setTimeout(() => this.show = false, 700);
  }

  ngOnInit() {
    if ( this.isLogger ) {
      this.logger.getData(LOGGER).subscribe( this.response() );
    } else {
      console.log = (...args: Array<any>) => {};
    }
  }

  response(): ( response ) => void {
    return response => {
      if ( !response ) {
        return;
      }
      response.fecha = new Date();
      response.oculto = true;
      this.log.push(response);
    };
  }

  filtro(): (item) => boolean {
    return (item): boolean => item.oculto === true;
  }

}
