import { Injectable } from '@angular/core';
import { SendInformationService } from 'src/app/modules/common/service/SendInformation/send-information.service';
import { switchMap, map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
import { CriptoService } from '../encryption/cripto.service';
import { AuthService } from '../authentication/auth.service';
import { environment } from 'src/environments/environment';
import { Router } from '@angular/router';
import { PAYLOAD, STEP_ID, MODAL, INITIAL, ONCALL, STATUS_NORMAL, IP, LOADING, NAVIGATE, LOGGER, STATE_SUCESS } from '../../CONST';
import { Modal } from '../../components/modal/modal';
import { AlertModalTMAComponent } from '../../components/entryComponents/alert-modal-tma/alert-modal-tma.component';
import { CatalogoService } from '../catalogo/catalogo.service';
import { ModalBuilderService } from '../ModalBuilder/modal-builder.service';
import { PostMessagesService } from '../postMessages/post-messages.service';
import { from, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class WorkflowService {
  payload: any;
  decryptWorwflowResponse: boolean;
  constructor(
    private setPayload: SendInformationService<{ [key: string]: any }>,
    private setCurrentStep: SendInformationService<string>,
    private modalService: SendInformationService<Modal>,
    private obser: SendInformationService<boolean>,
    private router: Router,
    private http: HttpClient,
    private cripto: CriptoService,
    private auth: AuthService,
    private catalogo: CatalogoService,
    private modalBuilder: ModalBuilderService,
    private postMessage: PostMessagesService
  ) {
    this.decryptWorwflowResponse = environment.decryptWorwflowResponse;
  }

  workflow(stepId: string, data: any) {
    this.payload = this.cleanUpSpecialChars(data);
    if (stepId === INITIAL) {
      this.payload.ipAddress = this.obser.lastValue(IP);
    }

    console.log('PAYLOAD enviado a WORKFLOW: ', this.payload);
    this.obser.sendData({
      title: `${this.obser.lastValue(STEP_ID)} => Workflow`,
      data: this.payload
    } as any, LOGGER);
    this.cripto.createEncryptor(this.cripto.getPublickeyMbaas());
    this.cripto.encrypter(this.payload).then(
      response => this.continueCallWorkflow(stepId, response)
    )
    .catch(err => {
      console.error('Error Cripto: ', err);
      this.errorGeneralWorflow()(err);
    });
  }

  private continueCallWorkflow(stepId: string, encPayload: any): void {
    console.log('cliente ===>stepId ', this.auth.getClientId(), stepId);
    const workflowInterface: any = {
      stepId,
      payload: encPayload,
      clientId: this.auth.getClientId(),
      status: 1,
      message: STATUS_NORMAL
    };
    console.log(workflowInterface);
    const httpOptions = this.auth.headerTokenInjector();
    const strObj = JSON.stringify(workflowInterface);
    console.log(strObj);
    const REST = this.http.post<any>(environment.workflowUrl, strObj, { headers: httpOptions });
    (this.decryptWorwflowResponse ? REST.pipe(switchMap( this.uncripSwitchMap())) : REST)
      .subscribe(this.successWorkflow(), this.errorGeneralWorflow());
  }

  uncripSwitchMap(): (res: any) => Observable<any> {
    return res => {
      return from(this.cripto.unencrypter(res.data.payload)).pipe(map( (data: any) => {
        res.data.payload = data;
        return res;
      } ));
    };
  }

  successWorkflow(): (response) => void {
    return response => {
      response.data.payload = this.cleanCharactersToUncripter(response.data.payload);
      setTimeout(() => this.obser.sendData({ mostrar: false, header: '', footer: ''} as any, LOADING), 2000);
      this.obser.sendData(false, ONCALL);
      this.obser.sendData({
        title: `Workflow => ${response.data.stepId}`,
        data: response
      } as any, LOGGER);

      this.setPayload.sendData(response.data.payload, PAYLOAD);
      if (response.data.status === STATE_SUCESS) {
        console.log('PAYLOAD recibido de WORKFLOW', response.data.payload);
        this.setPayload.sendData(response.data.payload, PAYLOAD); // borrar cuando se integre el Cripto
        this.setCurrentStep.sendData(response.data.stepId, STEP_ID);
        this.router.navigate([this.getRoute(response.data.stepId)]);
        return;
      }
      // modal con stepId en callback
      if (response.data.status === 6) {
        this.obser.sendData((stepID => {
          this.setCurrentStep.sendData(stepID, STEP_ID);
          this.router.navigate([this.getRoute(stepID)]);
        }) as any, NAVIGATE);
      }
      // tslint:disable-next-line: variable-name
      this.catalogo.data(response.data.message).subscribe( (_response: Modal) => {
        this.obser.sendData(false, ONCALL);
        this.obser.sendData(response.data.payload, PAYLOAD);
        this.modalService.sendData( this.modalBuilder.getModalData(_response[0], response.data.status), MODAL);
      });
    };
  }

  errorGeneralWorflow(): (err) => void {
    return err => {
      setTimeout(() => this.obser.sendData({ mostrar: false, header: '', footer: ''} as any, LOADING), 1000);
      this.obser.sendData(false, ONCALL);
      this.modalService.sendData(
        {
          display: true,
          buttons: [
            {
              callback: this.callbackModalError500(),
              buttonText: 'Finalizar',
              class: 'alertModal__footer--button alertModal__footer--button--primary'
            }
          ],
          title: 'Lo sentimos',
          message: 'En este momento no podemos atender su solicitud, por favor intente más tarde.',
          entryComponent: AlertModalTMAComponent
        }, MODAL
      );
    };
  }

  callbackModalError500(): (close: () => void) => void {
    return (close: () => void) => {
      close(),
      this.postMessage.appFinish('', 'op_home');
    };
  }

  cleanUpSpecialChars(data: any): any {
    return JSON.parse(
      JSON.stringify(data)
      .replace(new RegExp('&', 'g'), 'y')
      .replace(new RegExp('ñ', 'g'), 'n')
      .replace(new RegExp('Ñ', 'g'), 'N')
      .replace(new RegExp('Á', 'g'), 'A')
      .replace(new RegExp('á', 'g'), 'a')
      .replace(new RegExp('É', 'g'), 'E')
      .replace(new RegExp('é', 'g'), 'e')
      .replace(new RegExp('Í', 'g'), 'I')
      .replace(new RegExp('í', 'g'), 'i')
      .replace(new RegExp('Ó', 'g'), 'O')
      .replace(new RegExp('ó', 'g'), 'o')
      .replace(new RegExp('ú', 'g'), 'u')
      .replace(new RegExp('Ú', 'g'), 'U')
    );
  }

  cleanCharactersToUncripter(payload: any) {
    const strPayload = JSON.stringify(payload);
    const clenPayload = strPayload.replace(/aacutembaas/g, 'á')
      .replace(/eacutembaas/g, 'é')
      .replace(/iacutembaas/g, 'í')
      .replace(/oacutembaas/g, 'ó')
      .replace(/uacutembaas/g, 'ú')
      .replace(/Aacutembaas/g, 'Á')
      .replace(/Eacutembaas/g, 'É')
      .replace(/Iacutembaas/g, 'Í')
      .replace(/Oacutembaas/g, 'Ó')
      .replace(/Uacutembaas/g, 'Ú')
      .replace(/nacutembaas/g, 'ñ')
      .replace(/Nacutembaas/g, 'Ñ');
    return JSON.parse(clenPayload);
  }

  getRoute(stepID: string): string {
    return {
      ING001: 'cam/ingresos/ing001', // produtos
      PCL001: 'cam/ingresos/ing002', // ingreso

      BEN001: 'cam/beneficios/ben001', // beneficios

      CUE001: 'cam/tpc/tpc001', // tipo de cuenta

      VIN001: 'cam/vinculacion/vin001', // mis datos personales
      VIN002: 'cam/vinculacion/vin002', // datos personales
      VIN003: 'cam/vinculacion/vin003', // datos laborales
      VIN004: 'cam/vinculacion/vin004', // datos financieras
      VIN005: 'cam/vinculacion/vin005', // declaraciones
      VIN006: 'cam/vinculacion/vin006', // FATCA

      BIO001: 'cam/biometria/bio001', // biometria
      BIO002: 'cam/biometria/bio002', // biometria
      BIO003: 'cam/biometria/bio003', // biometria
      BIO004: 'cam/biometria/bio004', // biometria
      BIO005: 'cam/biometria/bio005', // biometria

      OTP001: 'cam/otp/otp001', // OTP

      CVI001: 'cam/clave/clv001', // clave virtual

      AUT001: 'cam/autorizaciones/aut001', // autorizaciones CTACAM
      AUT002: 'cam/autorizaciones/aut002',
      CRE016: 'cam/autorizaciones/lhc001', // autorizaciones El salvador
      CRE011: 'cam/autorizaciones/aut003', // autorizaciones Credito

      CRE001: 'cam/simulador/sim001', // config para la simulación
      CRE002: 'cam/simulador/sim002', // resultado de la simulación

      CRE004: 'cam/datos-credito/cre001', // datos-minimos
      CRE005: 'cam/datos-credito/cre003', // aprobación
      CRE012: 'cam/datos-credito/cre002', // felicitaciones

      CRE006: 'cam/apertura-credito/adc001', // credito aprovado => modal noCliente payload.esCliente
      CRE007: 'cam/apertura-credito/adc002', // contra oferta 1 => modal noCliente payload.esCliente
      CRE008: 'cam/apertura-credito/adc003', // contra oferta 2 => modal noCliente payload.esCliente
      CRE009: 'cam/apertura-credito/adc004', // monto inferior=>  modal noCliente payload.esCliente
      CRE010: 'cam/apertura-credito/adc005', // rechazado
      CUE003: 'cam/apertura/acu001', // Información de la cuenta
      CUE005: 'cam/apertura/acu002', // confirme la información de la cuenta
      CUE006: 'cam/apertura/acu003', // Apertura Exitosa

      ACU002: 'cam/apertura/acu002',
      ACU003: 'cam/apertura/acu003',
      ACU001: 'cam/apertura/acu001',
      CUE002: 'cam/apertura/acu001' // Información de la cuenta
    }[stepID] || 'forbidden';
  }
}
