import { Injectable } from '@angular/core';
import { APP_READY, CAPTURE_FRONT_DOCUMENT, CAPTURE_BACK_DOCUMENT, CAPTURE_FACE, APP_FINISH, REGISTER_PATH, LOGGER } from '../../CONST';
import { SendInformationService } from '../SendInformation/send-information.service';

@Injectable({
  providedIn: 'root'
})
export class PostMessagesService {
  constructor(
    private obser: SendInformationService<any>
  ) {
    this.addListener();
  }

  /*
    Listener para capturar las respuestas de los
    post messages que vienen del apk.
  */
  addListener() {
    window.addEventListener('message', event => {
      if (event.data.event === CAPTURE_FRONT_DOCUMENT) {
        this.obser.sendData({
          title: `event::${event.data.event}`,
          data: `event---${JSON.stringify(event.data)}`
        }, LOGGER);
        this.obser.sendData({
          title: `event::${event.data.event}`,
          data: `response:::${JSON.stringify(event.data.response)}`
        }, LOGGER);
      }
      const POST_MESSAGE_NAME = event.data.event;
      const POST_MESSAGE_RESPONSE = event.data.response;
      this.obser.sendData(POST_MESSAGE_RESPONSE, POST_MESSAGE_NAME);
    });
  }

  /* POST MESSAGES */
  appReady() {
    window.parent.postMessage({
      event: APP_READY,
    }, '*');
  }

  setTitle() {
    const title = prompt('Ingrese el titulo');
    if (title) {
      window.parent.postMessage({
        event: 'setTitle',
        title
      }, '*');
    }
  }

  registerPath(data: any) {
    window.parent.postMessage({
      event: REGISTER_PATH,
      code: data.code || null,
      description: data.description || null,
      serverEnrollmentKey: data.serverEnrollmentKey || null,
      uuidDevice: data.uuidDevice || null,
      uuidTransaction: data.uuidTransaction || null,
      typeEnrollment: data.typeEnrollment || null,
      userId: data.userId || null,
      userType: data.userType || null
    }, '*');
  }

  captureFrontDocument(serverEnrollmentKey: string, pathId: string) {
    window.parent.postMessage({
      event: CAPTURE_FRONT_DOCUMENT,
      serverEnrollmentKey,
      pathId
    }, '*');
  }

  captureBackDocument(serverEnrollmentKey: string) {
    window.parent.postMessage({
      event: CAPTURE_BACK_DOCUMENT,
      serverEnrollmentKey
    }, '*');
  }

  captureFace(serverEnrollmentKey: string) {
    window.parent.postMessage({
      event: CAPTURE_FACE,
      serverEnrollmentKey
    }, '*');
  }

  appFinish(serverEnrollmentKey: string, statusMessage: string = 'finish')  {
    console.log(`=============`);
    console.log(`event::${APP_FINISH}`);
    console.log(`serverEnrollmentKey::${serverEnrollmentKey}`);
    console.log(`statusMessage::${statusMessage}`);
    console.log(`=============`);
    window.parent.postMessage({
      event: APP_FINISH,
      serverEnrollmentKey,
      status: 'ok',
      statusCode: 200,
      statusMessage
    }, '*');
  }
}
