import { Component, OnInit, ViewChild, ViewContainerRef, ComponentFactoryResolver } from '@angular/core';
import { SendInformationService } from '../../service/SendInformation/send-information.service';
import { Modal } from '../modal/modal';
import { MODAL, PAYLOAD } from '../../CONST';
import { ViewEncapsulation } from '../encaptulation';

@Component({
  selector: 'app-modal',
  templateUrl: './modal.component.html',
  encapsulation: ViewEncapsulation.ShadowDom,
  styleUrls: ['./modal.scss']
})
export class ModalComponent implements OnInit {

  data: Modal = {
    buttons: [
      {
        callback: null,
        buttonText: ''
      }
    ],
    title: '',
    message: '',
    display: false,
    entryComponent: null
  };

  componentRef: any;
  @ViewChild('loadComponent', { read: ViewContainerRef }) entry: ViewContainerRef;
  constructor(
    private resolver: ComponentFactoryResolver,
    private modalSer: SendInformationService<Modal>,
    private obser: SendInformationService<{ [key: string]: any}>
  ) { }

  ngOnInit() {
    this.modalSer.getData(MODAL)
      .subscribe( this.response() );
  }

  response(): (response: Modal) => void {
    return (response: Modal) => {
      if ( response ) {
        this.data = response;
        this.data.data = this.obser.lastValue(PAYLOAD);
        this.createComponent();
        return;
      }
      this.data.display = false;
    };
  }

  createComponent() {
    this.entry.clear();
    this.componentRef = this.entry.createComponent(this.resolver.resolveComponentFactory( this.data.entryComponent ));
    this.componentRef.instance.data = this.data;
    this.componentRef.instance.destroy = this.desTRoy();
  }

  desTRoy(): () => void {
    return () => { this.componentRef.destroy(); };
  }
}
