import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'searchElement'
})
export class SearchElementPipe implements PipeTransform {

  transform(value: string, datos: Array<{[key: string]: any}>, comparar: string = 'value', mostrar: string = 'label'): any {
    if ( datos.length === 0) {
      return null;
    }
    let resultado: string = null;
    datos.map( item => {
      if ( value === item[comparar] ) {
        resultado = item[mostrar];
      }
      return item;
    });
    return resultado;
  }

}
