import { Injectable } from '@angular/core';
import { Modal } from '../../components/modal/modal';
import { AlertModalTMAComponent } from '../../components/entryComponents/alert-modal-tma/alert-modal-tma.component';
import { PostMessagesService } from '../postMessages/post-messages.service';
import { SendInformationService } from '../SendInformation/send-information.service';
import { ACTIONMODAL, ONCALL, NAVIGATE } from '../../CONST';

@Injectable({
  providedIn: 'root'
})
export class ModalBuilderService {

  constructor(
    private postMessage: PostMessagesService,
    private obser: SendInformationService<any>
  ) { }

  getModalData( response: any, errorType: any ): Modal {
    return {
      // 1: (response) => SUCCESS workflow
      // tslint:disable-next-line: no-shadowed-variable
      2: (response) => this.redirecTo(response),
      // tslint:disable-next-line: no-shadowed-variable
      3: (response) => this.simple(response),

      // 4: (response) => ERROR DE BIOMETRIA
      // tslint:disable-next-line: no-shadowed-variable
      5: (response) => this.action(response),
      // tslint:disable-next-line: no-shadowed-variable
      6: (response) => this.redirecByStepID(response)
    }[errorType](response);
  }

  redirecTo(response: any): Modal {
    return {
      display: true,
      buttons: response.buttons.map( item => {
        item.class = 'alertModal__footer--button alertModal__footer--button--primary';
        const url = item.callback.toString();
        item.callback = ( close ) => {
          // window.location.href = url || '';
          this.postMessage.appFinish('', url);
          this.obser.sendData(true, ONCALL);
          close();
        };
        return item;
      }),
      title: response.title,
      message: response.message,
      entryComponent: AlertModalTMAComponent
    };
  }

  simple(response: any): Modal {
    return {
      display: true,
      buttons: response.buttons.map( item => {
        item.class = 'alertModal__footer--button alertModal__footer--button--primary';
        item.callback = ( close ) => close();
        return item;
      }),
      title: response.title,
      message: response.message,
      entryComponent: AlertModalTMAComponent
    };
  }

  action(response: any): Modal {
    return {
      display: true,
      buttons: response.buttons.map( item => {
        item.class = 'alertModal__footer--button alertModal__footer--button--primary';
        const callback = item.callback.toString();
        item.callback = ( close ) => {
          this.obser.lastValue(ACTIONMODAL)(close, callback);
          // Esto se envia en la pantalla que necesites
          // this.obser.sendData((close, callback) => {
          //   // codigo a ejecutar
          //   console.log(');
          //   // se ejecuta cuando se quiera cerrar la modal.
          //   close();
          // }, ACTIONMODAL);
        };
        return item;
      }),
      title: response.title,
      message: response.message,
      entryComponent: AlertModalTMAComponent
    };
  }

  redirecByStepID(response: any): Modal {
    return {
      display: true,
      buttons: response.buttons.map( item => {
        item.class = 'alertModal__footer--button alertModal__footer--button--primary';
        const callback = item.callback.toString();
        item.callback = ( close ) => {
          this.obser.lastValue(NAVIGATE)(callback);
          close();
        };
        return item;
      }),
      title: response.title,
      message: response.message,
      entryComponent: AlertModalTMAComponent
    };
  }



}
