import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HttpClientModule, HttpClient, HTTP_INTERCEPTORS } from '@angular/common/http';
import { JwtModule } from '@auth0/angular-jwt';
import { LoaderComponent } from './modules/common/components/loader/loader.component';
import { SendInformationService } from './modules/common/service/SendInformation/send-information.service';
import { ModalComponent } from './modules/common/components/modal/modal.component';
import { CommonModule } from '@angular/common';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { AlertModalTMAComponent } from './modules/common/components/entryComponents/alert-modal-tma/alert-modal-tma.component';
import { InfoModalTMAComponent } from './modules/common/components/entryComponents/info-modal-tma/info-modal-tma.component';
import { ValidationRulesService } from './modules/common/service/validationRules/validation-rules.service';
import { WorkflowService } from './modules/common/service/workflow/workflow.service';
// import { BaseComponent } from './modules/common/components/base/base.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ToolTipComponent } from './modules/common/components/tool-tip/tool-tip.component';
import { ModalBuilderService } from './modules/common/service/ModalBuilder/modal-builder.service';
import { PassSecurityTrustPipe } from './modules/common/pipe/passSecurityTrust/pass-security-trust.pipe';
import { LoggerModule, LoggerComponent } from './logger/logger.index';
import { PipeModule } from './modules/common/pipe/pipe.module';
import { AuthService } from './modules/common/service/authentication/auth.service';
import { Router } from '@angular/router';
import { CriptoService } from './modules/common/service/encryption/cripto.service';
import { ReAuthenticateService } from './modules/common/service/interceptores/reAuthenticate/re-authenticate.service';
import { IpService } from './modules/common/service/interceptores/ip/ip.service';
import { PostMessagesService } from './modules/common/service/postMessages/post-messages.service';
import { LZStringModule, LZStringService } from 'ng-lz-string';
// import { environment } from 'src/environments/environment';

export function jwtTokenGetter() {
  return localStorage.getItem('access-token');
}
export function initTraslate(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

@NgModule({
  declarations: [
    AppComponent,
    LoaderComponent,
    ModalComponent,
    AlertModalTMAComponent,
    InfoModalTMAComponent
  ],
  imports: [
    CommonModule,
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    HttpClientModule,
    LoggerModule,
    PipeModule,
    FormsModule,
    ReactiveFormsModule,
    LZStringModule,
    JwtModule.forRoot({
      config: {
        tokenGetter: jwtTokenGetter,
        whitelistedDomains: ['.*']
      }
    }),
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: initTraslate,
        deps: [HttpClient]
      }
    }),
  ],
  exports: [TranslateModule],
  providers: [
    SendInformationService,
    ValidationRulesService,
    WorkflowService,
    ModalBuilderService,
    LZStringService,
    PostMessagesService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: IpService,
      deps: [SendInformationService],
      multi: true
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ReAuthenticateService,
      deps: [AuthService, SendInformationService, PostMessagesService, Router],
      multi: true
    }
  ],
  bootstrap: [AppComponent, LoaderComponent, ModalComponent, LoggerComponent],
  entryComponents: [AlertModalTMAComponent, InfoModalTMAComponent]
})
export class AppModule { }
