import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'productCode'
})
export class ProductCodePipe implements PipeTransform {

  transform(plainCreditCard: any, visibleDigits: number = 4): string {
    const maskedSection = plainCreditCard.slice(0, -visibleDigits);
    const visibleSection = plainCreditCard.slice(-visibleDigits);
    return maskedSection.replace(/./g, '*') + visibleSection;
  }
}
