import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'transpilleMessage'
})
export class TranspilleMessagePipe implements PipeTransform {

  transform(message: string, data: {[key: string]: any} = {}): any {
    return this.compileMessage(message, data);
  }

  compileMessage(message: string, data: {[key: string]: any}): string {
    let str = message;
    const patt = /{{[_a-zA-Z0-9]+(\.[_a-zA-Z0-9]+)*}}/g;
    const pattFor = /{forEach_[_a-zA-Z0-9]+(\.[_a-zA-Z0-9]+)*{.*}forEach}/g;
    const arrayFor = (str.match(pattFor) || []).map( item => {
      const items = ((f: string): string => {
        const pattArray = /{forEach_[_a-zA-Z0-9]+(\.[_a-zA-Z0-9]+)*{/;
        const coincidencias: Array<string> = f.match(pattArray);
        return coincidencias[0].substring(9, coincidencias[0].length - 1);
      })(item);
      let allContent = '';
      const arr = this.existeRuta(data, items);
      if ( arr !== null ) {
        for (let j = 0; j < arr.length; j++ ) {
          let content = item.substring(10 + items.length, item.length - 9).toString();
          (content.match(patt) || []).map( i => ({
            jsonKey: i.substr(2, i.length - 4),
            jsonHtml: i
          })).map( (i, k) => {
            content = content.replace(
              i.jsonHtml,
              i.jsonHtml.replace('index', `${j}`)
            );
            return item;
          });
          allContent = allContent + content;
        }
      }

      return {
        jsonKEY: allContent,
        jsonHTML: item
      };
    }).map( item => {
      str = str.replace(item.jsonHTML, item.jsonKEY);
      return item;
    });

    (str.match(patt) || []).map( item => ({
      jsonKey: item.substring(2, item.length - 2),
      jsonHtml: item
    })).map( item => {
      str = str.replace(
        item.jsonHtml,
        this.existeRuta(data, item.jsonKey)
      );
      return item;
    });
    return str;
  }

  existeRuta(data: any, path: string|Array<string>, step: number = 0): string {
    let paths: Array<string> = [];
    if ( typeof path === 'string' ) {
      paths = path.split('.');
    }
    if ( typeof path === 'object' ) {
      paths = path;
    }
    if ( data[paths[step]] !== undefined && step + 1 === paths.length ) {
      return data[paths[step]];
    }
    if ( data[paths[step]] ) {
      return this.existeRuta(data[paths[step]], paths, step + 1);
    }
    return null;
  }

}
